* {
  font-family: "Oswald", sans-serif;
}

.menu-center {
  width: 90%;
  margin: 0 auto;
  text-align: left;
  max-width: 996px;
  padding-top: 2rem;
  margin-bottom: 10rem;
}
.menu-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem 1rem;
}

@media screen and (max-width: 1000px) {
  .menu-list {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 750px) {
  .menu-list {
    grid-template-columns: auto;
  }
}
