* {
}
.contact-center {
  box-sizing: border-box;
  width: 90%;
  margin: 2rem auto;
  margin-bottom: 9rem;
  max-width: 996px;
  font-size: 2rem;
}
.contact-text {
  text-align: center;
  margin-bottom: 1rem;
}
.contact-grid-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
p span {
  font-weight: bold;
}

.contact-content {
  border: 1px solid black;
  width: 100%;
  height: 40rem;
  border-radius: 5px;
  font-size: 1rem;
  max-height: 40rem;
  line-height: 3rem;
  padding: 2rem;
}
.map {
  margin-top: 5rem;
}
.contact-content p {
}
.contact-form {
  width: 100%;
  border: 1px solid black;
  border-left: none;
  border-radius: 5px;
  padding: 1rem;
}
