.navbar {
  font-family: "Mochiy Pop P One", sans-serif;
  position: sticky;
  top: 0;
  left: 0;
}

.nav-center {
  background-color: rgba(231, 231, 231, 0.651);
  width: 90%;
  max-width: 996px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 15rem 15rem;
  justify-content: space-around;
  align-items: center;
}
.nav-logo-container {
  display: flex;
  align-items: center;
}
.nav-logo-container p {
  align-self: center;
  font-size: 1.2rem;
}
.logo-img {
  width: 100%;
  max-width: 4rem;
}
.nav-links-container {
  display: flex;
  justify-content: right;
  gap: 2rem;
}
.nav-link {
  color: black;
  text-decoration: none;
  transition: all 0.3s linear;
  font-size: 1.2rem;
}
.nav-link:hover {
  color: rgb(104, 41, 41);
  transform: scale(1.2);
}
.nav-button-container {
  display: none;

  justify-content: right;
  margin-right: 2rem;
}
.nav-button {
  font-size: 1.5rem;
  cursor: pointer;
}
.nav-button:hover {
  color: rgb(175, 175, 175);
}
.nav-button-links {
  background-color: rgba(231, 231, 231, 1);
  display: none;
  font-family: "Mochiy Pop P One", sans-serif;
  position: fixed;
  right: 50%;
  top: 30%;
  transform: translate(50%, -30%);
  width: 20rem;
  text-align: center;
  box-shadow: 0 0 5px 0 black;
  height: 12rem;
  align-items: center;
  border: 1px solid black;
  z-index: 5;
}
.nav-button-link:hover {
  background-color: rgba(177, 147, 111, 0.651);
}
.nav-button-link {
  box-shadow: 0 0 5px 0 black;
  height: 3rem;
  width: 50%;
  margin: auto;
}
.nav-close-button {
  position: absolute;
  right: 1rem;
  top: 0.5rem;
  font-size: 2rem;
  cursor: pointer;
}
.nav-close-button:hover {
  color: rgb(104, 41, 41);
}

.active {
  display: grid;
}

@media screen and (max-width: 600px) {
  .nav-links-container {
    display: none;
  }
  .nav-button-container {
    display: flex;
  }
}
