.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  font-family: "Mochiy Pop P One", sans-serif;
}
.footer-center {
  padding-top: 2rem;
  display: grid;
  background-color: rgba(231, 231, 231, 0.651);
  width: 90%;
  margin: 0 auto;
  text-align: center;
  max-width: 996px;
  grid-template-rows: 1fr 1fr;
}
.footer-social-icons {
  font-size: 2rem;
  align-self: center;
  cursor: pointer;
}
.footer-social-icon:hover {
  color: rgb(68, 61, 61);
}
