* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.card-container {
  border: 1px solid black;
  width: 20rem;
  height: auto;
  font-size: 1.2rem;
  justify-self: center;
  border-radius: 0.5rem;
  overflow: hidden;
}
img {
  width: 100%;
  object-fit: contain;
}
.img-container {
  height: 10rem;
  overflow: hidden;
}
.title {
  margin-top: 0.5rem;
  text-align: center;
  font-size: 1.5rem;
}
.title p {
  font-weight: bold;
}
.content {
  padding: 1rem;
  line-height: 1.7rem;
  display: grid;
}
.price {
  justify-self: center;
}
.ingredients {
  font-weight: bold;
}
.ingredients-container {
  padding-bottom: 0.4rem;
}
.price-bold {
  font-weight: bold;
}
