* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
}

.hero-center {
  text-align: center;
  width: 90%;
  margin: 4rem auto;
  max-width: 996px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.text-container {
  max-height: 40rem;
  padding-top: 2rem;
  margin-left: 2rem;
  line-height: 3rem;
  white-space: normal;
  width: 90%;
  height: 100%;
}
.hero-button {
  background-color: transparent;
  padding: 1rem 2rem;
  box-shadow: 0 0 2px 0 black;
  transition: all 0.2s linear;
  font-size: 1.2rem;
}
.hero-button:hover {
  background-color: rgba(88, 81, 66, 0.651);
  padding: 1rem 2rem;
  box-shadow: 0 0 10px 0 black;
  transform: scale(1.1);
}

.image-container {
  width: 90%;
  margin: 0 auto;
  max-height: 40rem;
  background: no-repeat;
  background-size: cover;
  background-position: center;
}
.grid-container {
  margin: 2rem auto;
  display: grid;
  height: 30rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "Doner Doner Doner Adana Adana Adana Adana Adana"
    "Doner Doner Doner Adana Adana Adana Adana Adana"
    "Doner Doner Doner Adana Adana Adana Adana Adana"
    "Lahmacun Lahmacun Lahmacun Lahmacun Lahmacun Sish Sish Sish"
    "Lahmacun Lahmacun Lahmacun Lahmacun Lahmacun Sish Sish Sish"
    "Lahmacun Lahmacun Lahmacun Lahmacun Lahmacun Sish Sish Sish"
    "Meatball Meatball Meatball Meatball Meatball Sish Sish Sish"
    "Meatball Meatball Meatball Meatball Meatball Sish Sish Sish";
}
.Doner {
  grid-area: Doner;
}
.Lahmacun {
  grid-area: Lahmacun;
}
.Adana {
  grid-area: Adana;
}
.Sish {
  grid-area: Sish;
}
.Meatball {
  grid-area: Meatball;
}
/* style={{ backgroundImage: `url(${backgroundImage})` }} */
.grid-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media screen and (max-width: 700px) {
  .hero-center {
    font-size: 0.8rem;
  }
}
@media screen and (min-width: 701px) {
  .hero-center {
    font-size: 1.2rem;
  }
}
